import { UseI18nFor, type MessageDictHTML } from '@/core/elfland-addon-message-manager/types'

type MSG = {
  html: MessageDictHTML
}

const res: MSG = {
  html: {
    mouseenter: [
      ['img', '', 'alt', UseI18nFor.B, (value: null | string | string[]) => {
        if (value === '') return ''
        else return false
      }]
    ]
  }
}

export default res
